___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --title-color: #ff8c00;
}

@font-face {
    font-family: "3dumb";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2'),
         url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
    padding: 0;
    margin: 0;
    background-color: rgb(255, 178, 127);
}

.container {
    width: 80%;
    height: 100vh;
    background-color: beige;
    margin: 0 auto;
}

#header-icon {
    height: 120px;
    display: flex;
    align-items: center;
    justify-content:center;
    gap: 5rem;

    background-color: beige;
    box-shadow: inset 0px -3px #ffa9a9;
}

.todoIcon {
    width: 80px;
    mix-blend-mode: multiply;
    transition: 25ms linear;
}

.todoIcon:hover {
    scale: 1.1;
    cursor: pointer;
}

#header-icon > p {
    font-size: 3rem;
    font-family: '3dumb';
    font-weight: bolder;
    color: var(--title-color);
}

.content {
    display: grid;
    grid-template-columns: 1fr 4fr;
}

.side-list {
    margin: 50px 20px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 20px;
    align-items: center;
}

.side-image {
    width: 3rem;
    height: 3rem;
    justify-self: end;
}

.side-text {
    font-family: '3dumb';
    font-size: 2rem;
    color: var(--title-color);
    font-weight: bold;
    justify-self: start;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;;;IAGI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,oBAAoB;IACpB;+DACsD;IACtD,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,SAAS;IACT,oCAAoC;AACxC;;AAEA;IACI,UAAU;IACV,aAAa;IACb,uBAAuB;IACvB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,SAAS;;IAET,uBAAuB;IACvB,kCAAkC;AACtC;;AAEA;IACI,WAAW;IACX,wBAAwB;IACxB,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,oBAAoB;IACpB,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,8BAA8B;IAC9B,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,eAAe;IACf,yBAAyB;IACzB,iBAAiB;IACjB,mBAAmB;AACvB","sourcesContent":["*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n}\r\n\r\n:root {\r\n    --title-color: #ff8c00;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"3dumb\";\r\n    src: url('../fonts/3dumb-webfont.woff2') format('woff2'),\r\n         url('../fonts/3dumb-webfont.woff') format('woff');\r\n    font-weight: normal;\r\n    font-style: normal;\r\n}\r\n\r\nbody {\r\n    padding: 0;\r\n    margin: 0;\r\n    background-color: rgb(255, 178, 127);\r\n}\r\n\r\n.container {\r\n    width: 80%;\r\n    height: 100vh;\r\n    background-color: beige;\r\n    margin: 0 auto;\r\n}\r\n\r\n#header-icon {\r\n    height: 120px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content:center;\r\n    gap: 5rem;\r\n\r\n    background-color: beige;\r\n    box-shadow: inset 0px -3px #ffa9a9;\r\n}\r\n\r\n.todoIcon {\r\n    width: 80px;\r\n    mix-blend-mode: multiply;\r\n    transition: 25ms linear;\r\n}\r\n\r\n.todoIcon:hover {\r\n    scale: 1.1;\r\n    cursor: pointer;\r\n}\r\n\r\n#header-icon > p {\r\n    font-size: 3rem;\r\n    font-family: '3dumb';\r\n    font-weight: bolder;\r\n    color: var(--title-color);\r\n}\r\n\r\n.content {\r\n    display: grid;\r\n    grid-template-columns: 1fr 4fr;\r\n}\r\n\r\n.si